import * as React from "react"
import SEO from "../components/seo";
import SyntaxHighlight from "../components/SynaxHighlight";
import Icon from "../components/Icon";
import { evaIconsFill } from "../components/IconsLibs/iconsEvaFill";

const npm =
    `npm install skil3e-css
yarn add skil3e-css`

const Index = () => {
    return (
        <>
            <SEO title={ "Home" }/>
            <h1>Introduction</h1>
            <p>Skil3e-css a simple modular css utility framework. It utilizes the new SASS <a href="https://sass-lang.com/documentation/at-rules" target="_blank">at-rules</a> that allows you to use only the part you need, the way you need them.</p>
            <h2>NPM / YARN</h2>
            <SyntaxHighlight lang={ "powershell" } codeString={ npm }/>
            {/*<h3>Implementation</h3>*/}
            {/*<p>On your main scss/sass file:</p>*/}
            {/*<p>Use the whole library.</p>*/}
            {/*<SyntaxHighlight lang={ "scss" } codeString={ `@use "~skil3e-css/src/skil3e";` }/>*/}
            {/*<p>Use only the basics.</p>*/}
            {/*<SyntaxHighlight lang={ "scss" } codeString={ `@use "~skil3e-css/src/basic";` }/>*/}
            <p className={ "p--sm brd brd--warning flex--center" }><Icon className={ "flex--center mr--md fill--warning" } icon={ evaIconsFill.alert_triangle }/> <span>Uses <a href="https://sass-lang.com/dart-sass" target={ "_blank" }>Dart-Sass</a> to compile.</span></p>
            <p>You can alias node-sass to dart-sass with:</p>
            <SyntaxHighlight codeString={`npm install node-sass@npm:sass`}/>
            <h2>CDN</h2>
            <SyntaxHighlight lang={ "html" } codeString={ `<link rel="stylesheet" href="https://unpkg.com/skil3e-css@latest/dist/skil3e.min.css" crossorigin="anonymous"/>` }/>
        </>
    )
}

export default Index
